.workflow {
  width: 70%;
  margin: 3rem;
}

code {
  font-family: Consolas, "Liberation Mono", Menlo, Monaco, Courier, monospace;
  background-color: #f5f5f5;
  color: #444
}

#content2 {
  display: inline-block;
  width: 100%;
}

pre {
  overflow-x: auto;
  width: 80%;
  font-family: 'Courier New', monospace;
  font-size: 16px;
  padding: 1rem;
  display: inline-block;
  border-radius: 2px;
  color: #2e2e2e;
  background-color: #f5f5f5;
  border: 2px solid #ccc;
}

.line {
  text-align: center;
}

.home-section button {
  -ms-flex-pack: center;
      justify-content: center;
  margin: 1.3rem;
  height: 4rem;
  width: 25%;
  font-size: 1rem;
  padding: 1rem;
  border-radius: 5px;
  background-color: dodgerblue;
  color: white;
  border: 2px solid #ccc;
}

#config img {
  width: 100%;
}

#config button {
  background-color: #2e2e2e;
}

#config button:focus {
  background-color: #2a0e8f;
}

#config button:hover {
  background-color: #2a0e8f;
  font-size: 1.3rem;
}

.download {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  width: 100%;
  margin-top: -8rem;
}

.download a {
  padding: 13px 32px;
  background-color: rgb(85, 83, 79);
  border-radius: .9rem;
  width: 20%;
  color: #fff;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  font-size: 22px;
  font-family: OpenSans, sans-serif;
  line-height: 36px;
  font-weight: 100;
}

#content1 ul {
  width: 80%;
}

.fine-print {
  font-size: .85rem;
  padding-right: 1rem;
  margin: 0 0 1rem 0;
}


